@import url(https://fonts.googleapis.com/css?family=Raleway:100,200,300,regular,500,600,700,800,900,100italic,200italic,300italic,italic,500italic,600italic,700italic,800italic,900italic);
@import url(https://fonts.googleapis.com/css?family=Tektur:regular,500,600,700,800,900);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #f0f0f0;
  color: #212529;
  font-family: Raleway, sans-serif;
}

.wrapper {
  display: flex;
  flex-direction: column;
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
  height: 100vh;
}

a {
  text-decoration: none;
  color: #B09361;
}
a:hover {
  color: #49729C;
  cursor: pointer;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 10px 20px;
  background: #212529;
  color: #f0f0f0;
  font-family: Tektur, sans-serif;
}
.header-navigation {
  display: flex;
  justify-content: center;
  flex: 1;
}
.header-nav {
  display: flex;
  list-style: none;
  font-size: 1.5rem;
}
.header-nav li {
  margin: 0 20px;
}

.main {
  display: flex;
  flex: 1;
  padding: 10px 20px;
  border-right: 2px solid #212529;
  border-left: 2px solid #212529;
}

.contacts {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 10px 20px;
}
.contacts input {
  padding: 5px;
  margin: 5px 0;
}

.message {
  padding: 5px;
}

.footer {
  display: flex;
  align-items: center;
  height: 60px;
  padding: 10px 20px;
  background: #212529;
  color: #f0f0f0;
}